  font-family: 'SEGA LOGO FONT';
  font-style: normal;
  font-weight: normal;
  src: local('SEGA LOGO FONT'), url(/robofriendsdemo/static/media/SEGA.12a3db8b.woff) format('woff');
}
h1 {
  font-family: 'SEGA LOGO FONT';
  font-weight: 200;
  color: #a40606;
}

